.animate__animated.animate__bounceInRight{-webkit-animation-duration:2s;animation-duration:2s}.tabs{border-radius:20px 0px 0px 0px;position:absolute;height:50px;width:250px;bottom:35%;right:0px;z-index:1;display:flex}.tabs__item{color:rgba(255,255,255,0.616);font-size:26px;display:flex;justify-content:center;align-items:center;width:50%;height:100%}.tabs__item:nth-of-type(2){border-left:1px rgba(255,255,255,0.5) solid}.tabs__item:hover{cursor:pointer}.tabs__item--active{color:white;border-bottom:2px rgba(255,255,255,0.829) solid}@media screen and (max-width: 850px){.tabs{bottom:33%}}@media screen and (max-width: 578px){.tabs{bottom:0%;border-radius:0px 0px 0px 0px;width:100%}}.current{display:flex;position:relative;padding-top:40px;padding-left:80px;padding-right:50px;padding-bottom:30px;width:100%;height:100%;padding-bottom:36%}@media screen and (max-width: 768px){.current{flex-wrap:wrap}}@media screen and (max-width: 768px){.current{padding:0;justify-content:center;align-items:center}}@media screen and (max-width: 578px){.current{align-items:flex-start;min-height:100vh}}.current__timestamp{font-size:60px}.current__day{font-size:25px}.current__time{color:white;padding-bottom:20px}@media screen and (max-width: 768px){.current__time{padding-bottom:100px;display:flex;flex-direction:column;align-items:center}}@media screen and (max-width: 768px){.current__time{padding-bottom:40px}}.current__seconds{padding-left:8px;font-size:40px}.current__info-container{width:50%;height:100%}@media screen and (max-width: 768px){.current__info-container{width:100%;order:1}}@media screen and (max-width: 578px){.current__info-container{height:100%}}.current__info{color:white;display:flex;flex-wrap:wrap;flex-direction:column;justify-content:space-around;height:90%}@media screen and (max-width: 768px){.current__info{width:100%;margin:auto;order:1;flex-wrap:wrap;max-height:110px;align-items:center}}@media screen and (max-width: 578px){.current__info{max-height:50%}}.current__item{display:in