.forecast-daily{justify-content:center;transition:1.4s all ease;display:flex;flex-wrap:wrap;width:100%;height:35%;position:absolute;bottom:0px;left:150%}@media screen and (max-width: 850px){.forecast-daily{height:33%}}@media screen and (max-width: 578px){.forecast-daily{display:none;position:static;flex-wrap:wrap;align-items:center;justify-content:center}}.forecast-daily--active{left:0px}@media screen and (max-width: 578px){.forecast-daily--active{display:flex}}.forecast-daily__hour{padding-top:20px;color:white;align-content:center;align-items:center;justify-content:center;display:flex;flex-wrap:wrap;width:16.65%;height:50%;border-bottom:1px rgba(255,255,255,0.39) solid}@media screen and (max-width: 578px){.forecast-daily__hour{width:33%}}.forecast-daily__time{font-size:20px;display:flex;align-items:flex-end;justify-content:center;text-align:center;width:100%}.forecast-daily__temp{font-size:20px;height:50%;display:flex;align-items:center}.forecast-daily__icon-b{margin-left:-10px;max-width:40%;height:50%}.forecast-daily__icon{max-width:100%}

